/* WordCloud.css */
.word-cloud {
  position: relative;
  width: 90%; /* 设置词云容器的宽度 */
  height: 300px; /* 设置词云容器的高度 */
  background-color: #f7f7f7; /* 背景颜色 */
  border-radius: 10px;
  margin: 0 auto;
  overflow: hidden;
  border: 1px solid #ddd;
  margin-top: .3rem;
  padding-top: .3rem;
}

.word {
  position: absolute;
  transform: translate(-50%, -50%); /* 让词语的中心对齐指定位置 */
  transition: transform 0.3s ease, color 0.3s ease;
}

.title{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: .34rem;
  color:#666A70;
  img{
    width: .4rem;
    height: .4rem;
    margin-right: .1rem;
  }
}



@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}


.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* 或者根据需要调整高度 */
}

.loading img {
  width: 100px; /* 根据实际图片大小调整 */
  height: 100px;
}

