body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dialogue-container {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: auto;
  padding: 10px;
  background-color: #F2F4F6;
  padding-bottom: 1rem;
}

.avatar{
  width: .8rem;
  height: .8rem;
  background: url('./assets/head-bg.png') no-repeat center center;
  display: flex;
  justify-content: center;
  align-items: center;
  border:1px solid #ccc;
  border-radius: 50%;
  margin-right: .2rem;
}

.message {
  padding: 10px;
  border-radius: .2rem;
  word-wrap: break-word;
  font-size: .32rem;
  line-height: .5rem;
}

.message-container{
  max-width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: .2rem 0;
}

.message-container.left {
  align-self: flex-start;
  background-color: #ffffff;
  border-radius: .2rem;
}

.message-container.right {
  align-self: flex-end;
  color: white;
}

.message.message-right{
  background-color: #A2BA83;
}

.talk-container{
  position: fixed;
  bottom: 3%;
  width: 88%;
  height:5%;
}

.contact-talk{
  border: 1px solid #D0DEBE;
  border-radius: 0.15rem;
  height:100%;
  font-size: .32rem;
  width: 100%;
  padding-left: .2rem;
}

.talk-img{
  position: fixed;
  bottom: 2.5%;
  right: 7%;
  width: 0.5rem;
  height: 0.5rem;
}

.icon-slider{
  width: 100%;
  overflow-x: scroll;
  scrollbar-width: none; /* 隐藏滚动条（适用于Firefox） */
  -ms-overflow-style: none; /* 隐藏滚动条（适用于IE和Edge） */
  white-space: nowrap; /* 防止图标换行 */
}

/* 图标容器样式 */
.icon-container {
  display: flex;
  justify-content: flex-start; /* 确保图标从左到右排列 */
  align-items: center;
  width: fit-content;
  height: 1.7rem;
}
.icon-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.icon-count{
  font-size: .32rem;
  color:#454A55;
  margin-top: .2rem;
}
.icon-container::-webkit-scrollbar {
  display: none; /* 隐藏滚动条（适用于Chrome、Safari和Opera） */
}

/* 图标样式 */
.icon-bg {
  width: 1rem; /* 图标宽度 */
  height: 1rem;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin: 0 .2rem; /* 图标之间的间距 */
  transition: all 0.3s ease; /* 过渡效果 */
  cursor: pointer; /* 鼠标指针样式 */
  border: 1px solid transparent; /* 默认边框为透明 */
}

.icon-bg.active {
  border: 1px solid #C8C8C8; /* 激活时添加边框 */
  transform: scale(1.2); /* 激活时放大图标 */
}

.icon-bg img {
  width: 0.8rem; /* 图标宽度 */
  height: 0.8rem; /* 图标高度 */
  border-radius: 50%;
}

.emotion-labels {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.emotion-label {
  width: 100%;
}

.label-text {
  font-weight: bold;
  margin-bottom: 0.5rem;
  font-size: .32rem;
  margin-bottom: .1rem;
}

.label-texts {
  list-style-type: none;
  padding: 0;
  margin-top: .2rem;
}

.label-text-item {
  margin-bottom: 0.2rem;
  font-size: .26rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color:#454A55;
}
.label-text-item-content{
 border-radius: .2rem;
  padding: .2rem;
  line-height: .3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 70%;
  background-color: #ffffff;
}