@import './layout.css';
@import './reset.css';
.home{
  display: flex;
  position: relative;
  height: 100vh;
}

.tree-content{
  width: 100%;
  padding-top: 30%;

  .tree-item-0,.tree-item-1,.tree-item-2,.tree-item-3,.tree-item-4,.tree-item-5,.tree-item-6,.tree-item-7{
    position: absolute;
    top:2%;
    left: 40%;

    .tree-item-tags{
      background-size: cover;

      
    }

    .tree-item-title{
      font-size: .24rem;
      position: absolute;
      left: 50%;
      margin-left: -.4rem;
    }

    .tree-item-tags{
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 3.04rem;
      height: 1rem;
      padding-left: 1.3rem;
      padding-top: .3rem;
    }

    .tree-item-tag{
      background-color: #ffffff;
      border-radius: .2rem;
      font-size: .2rem;
      width: 1.28rem;
      height: 0.32rem;
      line-height: .3rem;
      text-align: center;
    }

    .tree-item-img{
      width: 0.8rem;
      height: 0.8rem;
      position: absolute;
      top: 0.1rem;
      left: 0.3rem;
  
        img{
          width: 100%;
          height: 100%;
        }
      }

    .tree-item-add{
      width: 0.52rem;
      height: 0.52rem;
      position: absolute;
      right: -.2rem;
      top: 0.5rem;
    }

    .tree-item-count{
      position: absolute;
      bottom: 0.1rem;
      font-size: 0.2rem;
      border-radius: 50%;
      background: #54C2F0;
      color: #ffffff;
      width: .3rem;
      height: .3rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

  

  }

  .tree-item-1{
    top:10%;
    left: 12%;
     .tree-item-count{
      background: #FF934A;
    }
  }

  .tree-item-2{
    top: 18%;
    left: 45%;
     .tree-item-count{
      background: #787F50;
    }
  }

  .tree-item-3{
    top: 32%;
    left: 20%;
     .tree-item-count{
      background: #936969;
    }

    .tree-item-tag{
      color:#936969 !important;
    }
  }

  .tree-item-4{
    top: 42%;
    left: 50%;
     .tree-item-count{
      background: #FF894A;
    }

    .tree-item-tag{
      color:#FF894A !important;
    }
  }

  .tree-item-5{
    top: 58%;
    left: 40%;
     .tree-item-count{
      background: #7C5E52;
    }

    .tree-item-tag{
      color:#7C5E52 !important;
    }
  }

  .tree-item-6{
    top: 68%;
    left: 20%;
     .tree-item-count{
      background: #936969;
    }

    .tree-item-tag{
      color:#936969 !important;
    }
  }

  .tree-item-7{
    top: 78%;
    left: 22%;
     .tree-item-count{
      background: #936969;
    }

    .tree-item-tag{
      color:#936969 !important;
    }
  }

  .tree-item-button{
    position: absolute;
    bottom: 5%;
    width: 100%;
    padding: 0 .3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: .2rem;

    p{
      font-size: .3rem;
      width: 1.5rem;
      height: 1.5rem;
      text-align: center;
      line-height: 1.5rem;
    }

    .activity{
      background: url('../assets/activity.png') no-repeat center center;
      background-size: contain;
      color:#000000;
    }
    .default{
      background: url('../assets/default.png') no-repeat center center;
      background-size: contain;
      color: rgba( #000000, 0.5);
    }
    .link{
      background: url('../assets/jump-bg.png') no-repeat center center;
      background-size: contain;
    }
  }



  .talk{
    position: fixed;
    bottom: 1%;
    left: 5%;
    border: 1px solid #D0DEBE;
    border-radius: 0.15rem;
    height: 0.2rem;
    width: 88%;
    height: 0.6rem;
    font-size: 0.24rem;
    padding-left: 0.1rem;
  }

  .talk-img{
    position: fixed;
    bottom: 1.5%;
    right: 7%;
    width: 0.5rem;
    height: 0.5rem;
  }

}

