.custom-input {
  /* 去除边框 */
  border: none;
  /* 去除轮廓线 */
  outline: none;
  /* 去除背景色 */
  background-color: transparent;
  /* 去除默认的内边距和外边距 */
  padding: 0;
  margin: 0;
  /* 设置字体大小和颜色 */
  font-size: inherit;
  color: inherit;
  /* 其他自定义样式 */
  width: 100%;
  height: 100%;
}